<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full d-flex">
                    <h4 class="modal-title">{{ $t('Manage') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                      <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js" v-if="reqFrom=='lineitem' || !IsSbaSelect">
                                    <div class="con" v-html="$t('User_Guide_Invoice_Change_File_Value_Invoice')"></div>
                                </div>
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js" v-else>
                                    <div class="con" v-html="$t('User_Guide_Invoice_Change_File_Value')"></div>
                                </div>
                            </div>
                        </span>
                </div>

                <div class="modal-body">
                <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')" > 

                </dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { locateBillingService } from "../../../services/locateBillingService";
export default {
    props: {
        ColumnIds: {
            type: String,
            required: true
        },
        reqFrom: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [{
                layoutType: "double",
                Data: [{
                        astype: "SelectField",
                        label: this.$t('fieldName'),
                        name: "FieldName",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],
                            onChange: this.GetFieldType
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Value'),
                        name: "Value_Select",
                        value: "",
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: []
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "NumericField",
                        label: this.$t('Value'),
                        name: "Value_Number",
                        visibility: false,
                        value: "",
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    },
                    {
                            astype: "DateField",
                            label: this.$t('Value'),
                            name: "Value_Date",
                            visibility: false,
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                    },
                    {
                        astype: "SearchableSelectField",
                        label: this.$t('CONTRACTNAME'),
                        name: "ContractName",
                        value: "",
                        placeholder: "Select Client Name",
                        visibility: false,
                        isSearch: false,
                        config: {
                            options: [],
                            actionButtons: { selectAll: true, active: true, inactive: true, clear: true },
                            groupFields: ['groupText'],
                            isMultiple: false,
                            minSearchLength:0,
                            statusFieldConfig: {
                            fieldName: 'status',
                            activeValues: ['Active', 'active'],
                            inactiveValues: ['Inactive', 'inactive'],
                            activeColor: '#28a745',
                            inactiveColor: '#dc3545'
                        },
                         onChange: this.onSelectionChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('UtilityName'),
                        name: "UtilityName",
                        value: "",
                        visibility: false,
                        placeholder: "",
                        config: {
                            options: [],
                            onChange: this.onSelectionUtilityChange
                        },
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('sba_id'),
                        name: "SBAId",
                        value: "",
                        visibility: false,
                        placeholder: "",
                        config: {
                            options: []
                        },
                        validationRules: {
                            "required": true
                        },
                    }

                ],
            }, 
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Comment",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            config: {
                                rows: 4
                            },
                            validationRules: { "required": true, max: 2000 },
                        },
                    ]
                }],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save and Process",
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            FieldOptions: [],
            DataType: 'nvarchar',
            SelectedValue: '',
            IsSbaSelect: false,
            SbaOptions:[]
        }
    },
    created: async function () {
        await this.GetFieldsListBySetupId();
        await this.fetchclientlist();
        //await this.GetSbaIdListByUtilityId()
    },
    methods: {
          onSelectionChange(value,x) {
            // Ensure value is an array for multiple mode
            var vm = this;
            vm.isLoading = true;
            var url = `clientId=${x}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url).then(response => {
                vm.isLoading = false;
                  let UtilityList=[];
                  if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.UTILITY_NAME,
                                value: item.UTILITY_ID
                            };
                            UtilityList.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("UtilityName", "config.options", UtilityList);
                        vm.$refs.dynamicform.UpdateKeyValue();
                         vm.UtilityId = vm.$refs.dynamicform.getPropertyValue("UtilityId", "value");
                        vm.GetSbaIdListByUtilityId();
                    }
                }
            }).catch(error => {
                vm.isLoading = false;
                vm.UtilityList = []; 
                vm.$refs.dynamicform.updateProperty("UtilityName", "config.options", UtilityList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        onSelectionUtilityChange(value,x) {
            var vm = this;
            vm.UtilityId = x.value;
            vm.GetSbaIdListByUtilityId();
        },
         GetFieldType: async function (x, y, z) {
            var vm = this;
            if (y.value) {
                let id = y.value;
                let filtered = vm.FieldOptions.filter(x => x.TEMPLATE_ID === id);
                vm.DataType= filtered[0].DATA_TYPE;
                vm.SelectedValue= filtered[0].COLUMN_NAME;
                if((vm.SelectedValue =='sba_id' || vm.SelectedValue == 'SBA_ID') && vm.reqFrom=='catchall'){
                    vm.IsSbaSelect=true;
                    vm.$refs.dynamicform.updateProperty("Value_Number", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Date", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Select", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("ContractName", "visibility", true);
                    vm.$refs.dynamicform.updateProperty("UtilityName", "visibility", true);
                    vm.$refs.dynamicform.updateProperty("SBAId", "visibility", true); 
                    await vm.fetchclientlist();
                }else{
                     vm.IsSbaSelect=false;
                    if(vm.DataType=='nvarchar'){
                    vm.$refs.dynamicform.updateProperty("Value_Number", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Date", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Select", "visibility", true); 
                    vm.$refs.dynamicform.UpdateKeyValue();
                    await vm.LoadFlagFieldDropDown();
                }else if(vm.DataType=='bigint' || vm.DataType=='int' || vm.DataType=='float' || vm.DataType=='decimal'){
                    vm.$refs.dynamicform.updateProperty("Value_Date", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Select", "visibility", false); 
                    vm.$refs.dynamicform.updateProperty("Value_Number", "visibility", true);
                }else if(vm.DataType=='datetime'){
                    vm.$refs.dynamicform.updateProperty("Value_Select", "visibility", false); 
                    vm.$refs.dynamicform.updateProperty("Value_Number", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("Value_Date", "visibility", true);
                }
                    vm.$refs.dynamicform.updateProperty("ContractName", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("UtilityName", "visibility", false);
                    vm.$refs.dynamicform.updateProperty("SBAId", "visibility", false); 
                }       
                vm.$refs.dynamicform.UpdateKeyValue();
            }
        },
         LoadFlagFieldDropDown: function () {
            var vm = this;
            let selctedValue = [];
            var url = `reqFrom=${vm.SelectedValue}`;
            locateBillingService.LoadFlagFieldDropDown(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.VALUE
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("Value_Select", "config.options", selctedValue);
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
         GetSbaIdListByUtilityId: function () {
            var vm = this;
            let selctedValue = [];
            vm.SbaOptions=[]
            var url = `UtilityId=${vm.UtilityId}`;
            locateBillingService.GetSbaIdListByUtilityId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.NAME,
                                value: item.SBA_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("SBAId", "config.options", selctedValue);
                        vm.SbaOptions=selctedValue;
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
        GetFieldsListBySetupId: function () {
            var vm = this;
            let selctedValue = [];
            var ReqFrom='catchall';
            if(vm.reqFrom=='catchall' || vm.reqFrom == "unprocessed"){
                ReqFrom='catchall';
            }else{
                ReqFrom='lineitem';
            }
            var url = `setupId=32&reqFrom=${ReqFrom}`;
            locateBillingService.GetFieldsListBySetupId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    vm.FieldOptions=response.data;
                    if (json != null) {
                        json.forEach(item => {
                            let obj = {
                                name: item.COLUMN_NAME,
                                value: item.TEMPLATE_ID
                            };
                            selctedValue.push(obj);
                        });
                        vm.$refs.dynamicform.updateProperty("FieldName", "config.options", selctedValue);
                        if(vm.reqFrom=='lineitem'){
                            vm.$refs.dynamicform.updateProperty("Comment", "visibility", true); 
                            vm.buttons[0].isVisible=false;
                        }else{
                            vm.$refs.dynamicform.updateProperty("Comment", "visibility", false); 
                            vm.buttons[0].isVisible=true;
                        }
                        vm.$refs.dynamicform.UpdateKeyValue();
                    }
                }
            });
        },
         fetchclientlist: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `reqFrom=CUSTOM`;
            locateBillingService.getClientList(url).then(response => {
                vm.isLoading = false;
                vm.clientList = response.data.items;
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
            }).catch(error => {
                vm.isLoading = false;
                vm.clientList = []; 
                vm.$refs.dynamicform.updateProperty("ContractName", "config.options", vm.clientList);
                vm.$refs.dynamicform.UpdateKeyValue();
                console.error('Error fetching client list:', error);
            }).finally(() => {
                vm.isLoading = false;
            });
        },
        Close: function () {
            this.$parent.IsChangeLineItem = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var beSbaId = 0;
            var value = "";
            var SelectedValue="";
            var SBASelectedValue="";
            let filtered = vm.FieldOptions.filter(x => x.TEMPLATE_ID === formfields.FieldName);
            SelectedValue= filtered[0].COLUMN_NAME;
            if(formfields.SBAId){
                let filtered = vm.SbaOptions.filter(x => x.value === formfields.SBAId);
                SBASelectedValue= filtered[0].name;
            }
            if (vm.reqFrom == "catchall" && (SelectedValue == "SBA_ID"|| SelectedValue == "sba_id")) {
                beSbaId = formfields.SBAId;
                value = SBASelectedValue;
            }
            else {
                if (formfields.Value_Date) {
                value = formfields.Value_Date;
                } else if (formfields.Value_Number) {
                value = formfields.Value_Number;
                } else if (formfields.Value_Select) {
                value = formfields.Value_Select;
                }
            }
             var setClient = 0;
            if (vm.reqFrom == "unprocessed") {
                setClient = 1
            }
            let text = val.replace(/<[^>]*>/g, '').trim();
            var reqFrom=text=='Save'? vm.reqFrom :'savaandprocess' ;
            var postJSON = {
                value: value,
                fieldName: SelectedValue,
                lineIds: vm.ColumnIds,
                reqFrom: reqFrom,
                setClient: setClient,
                beSbaId: beSbaId,
                Comment: formfields.Comment
            };
            if(vm.reqFrom=='catchall' || vm.reqFrom == "unprocessed"){
            locateBillingService.SaveColumnItems(postJSON).then(async function (response) {
                if (response.data.msg == 'Success') {
                    vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                    if(vm.reqFrom == "unprocessed"){
                        await vm.$parent.FetchUnprocessedRecords();
                    }else{
                        setTimeout(async () => {
                          await vm.$parent.FetchFlagRecords();
                        }, 1000);
                
                    }
                    vm.Close();
                } else {
                      vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
                }

            });
            }else{
                locateBillingService.SaveAndProcessLineItemsForInvoice(postJSON).then(async function (response) {
                if (response.data.msg == 'Success') {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('FlagRecords')]), "Success", true, vm.$t('Alert'));
                    
                    vm.Close();
                } else {
                      vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t("Alert"));
                }

            });
            }
        }
    }
}
</script>
